@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600);
* {
  box-sizing: border-box; }

body {
  font-family: "Open Sans", sans-serif;
  font-weight: 400; }

.container {
  margin: 0 auto;
  width: 50rem; }

h1, h2, h3, h4, h5 {
  font-weight: 600; }

h1 {
  font-size: 1.8rem; }

h2 {
  font-size: 1.6rem; }

h3 {
  font-size: 1.4rem; }

h4 {
  font-size: 1.2rem; }

h5 {
  font-size: 1rem; }

.alert-box {
  padding: 1.2rem 1rem; }

.alert-box__red {
  background: #e74c3c;
  color: white; }

button {
  background: #9b59b6;
  border: none;
  color: white;
  cursor: pointer;
  display: block;
  font-size: 1.2rem;
  padding: 0.8rem 1rem;
  transition: all .2s;
  width: 100%; }

.btn {
  background: #9b59b6;
  border: none;
  border-radius: 200px;
  color: white;
  display: inline-block;
  font-size: 1.2rem;
  padding: 1.2rem 3rem;
  text-decoration: none;
  transition: all .2s; }

button:hover, .btn:hover {
  background: #8e44ad; }

nav {
  background: #1abc9c;
  padding: 1rem; }

nav div.container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between; }

nav a {
  color: white;
  text-decoration: none; }

nav ul li {
  display: inline; }

nav ul li a {
  color: white;
  padding: 0 1rem;
  text-decoration: none; }

.task-list {
  margin-top: 1rem; }

.task-list__header {
  background: #34495e;
  color: white; }

.task-list__header div {
  font-weight: bold; }

.task-list__item, .task-list__header {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  height: 3.2rem;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 1rem; }

.task-list__item div:first-of-type, .task-list__header div:first-of-type {
  text-align: left;
  width: 50%; }

.task-list__item div:nth-of-type(2), .task-list__header div:nth-of-type(2) {
  text-align: right;
  width: 25%; }

.task-list__item div:nth-of-type(3), .task-list__header div:nth-of-type(3) {
  text-align: right;
  width: 25%; }

.task-list__item:nth-of-type(odd) {
  background: #ecf0f1; }

.task-list__item button {
  display: inline-block;
  font-size: 0.8rem;
  padding: 0.4rem 1rem;
  width: auto; }

.user-form {
  border: 1px solid #ecf0f1;
  border-radius: 10px;
  display: block;
  margin: 10rem auto;
  padding: 3rem 2rem;
  width: 28rem; }

.user-form h3 {
  margin-bottom: 2rem;
  text-align: center; }

.user-form input {
  border: 1px solid #ecf0f1;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.08);
  display: block;
  font-size: 1.2rem;
  margin: 0.75rem 0;
  padding: 0.75rem 1.5rem;
  width: 100%; }

.user-form p {
  margin-top: 2rem;
  text-align: center; }

.dashboard {
  padding: 2rem 0; }

.dashboard h3 {
  margin-bottom: 1.75rem; }

.dashboard hr {
  margin-bottom: 1.75rem; }

.welcome {
  padding: 4rem 0; }

.welcome h1 {
  margin-bottom: 0.5rem; }

.welcome h4 {
  margin-bottom: 1rem; }

