* {
  box-sizing: border-box;
}

body {
  font-family: $font-body;
  font-weight: 400;
}

.container {
  margin: 0 auto;
  width: 50rem;
}

h1, h2, h3, h4, h5 {
  font-weight: 600; 
}

h1 {
  font-size: 1.8rem;
}
h2 {
  font-size: 1.6rem;
}
h3 {
  font-size: 1.4rem;
}
h4 {
  font-size: 1.2rem;
}
h5 {
  font-size: 1rem;
}